        margin-left: 5px;
    }

    .k-buttons-action-options-tutorial {
        background-image: url(data:image/svg+xml;base64,);
    }

    .k-buttons-action-options-select {
        background-image: url(data:image/svg+xml;base64,);
    }

    .k-buttons-action-options-clear {
        background-image: url(data:image/svg+xml;base64,);
    }
</style>